



















import axios from 'axios';
import { Component, Vue, Provide } from 'vue-property-decorator';

@Component({
})

export default class Navbar extends Vue {
	// data
	public displayError: any = 'none';
	public displaySuccess: any = 'none';
	public todos: object[] = [
		{
			'text': 'Все юзеры',
			'component': '/all-users',
			'--before': '"\\f007"',
		},
		{
			'text': 'Карты',
			'component': '/maps',
			'--before': '"\\f041"',
		},
		{
			'text': 'Заметки',
			'component': '/notes',
			'--before': '"\\f040"',
		},
		{
			'text': 'Документация',
			'component': '/docs',
			'--before': '"\\f15c"',
		},
	];
	public async created() {
		await axios
		.post('api/v1/data/', {
			token: sessionStorage.getItem('token'),
		})
		// ответ на запрос
		.then(async (response) => {
			this.displaySuccess = 'grid';
			await axios
				.post('api/v1/data/all', {
					token: sessionStorage.getItem('token'),
				})
				// ответ на запрос
				.then((response) => {
					this.$store.commit('all', response.data.all);
					const doc = response.data.all.filter(function(row: any) {
						return Object.keys(row).some(function(key: any) {
							return (
								String(row[key])
									.toLowerCase()
									.indexOf('admin0') > -1
							);
						});
					});
					this.$store.commit('doc', doc['0'].name);
				});
			})
			// обработка ошибок
			.catch((e) => {
				console.log(e);
				this.displayError = 'block';
			});
	}
}
